$colorP : #000;
$colorL: #fff;
$colorL2: #fadb0f;

@mixin font($font-family, $font-file) {
  @font-face {
    font-family: $font-family;
    src: url($font-file+'.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
  } 
}


@include font('Starjedi', '../fonts/Starjedi');

body {
  background: $colorP;
}

.App{
  text-align: center;
}

label { 
  color: $colorL2;
  font-family: 'Starjedi';
}

.App header img {
  width: 30%;
  height: 30%;
}

.planetImg {
  pointer-events: none;
}

.content{
  background:rgba(9, 120, 180, 0.06);
  width: 300px;
  height: 50%;
  margin: 0px auto;
  /*box-shadow: 0px 1px 10px 0px rgba(130, 162, 251, 0.25), 0px 0px 3px 1px rgba(50, 0, 183, 0.7);*/
  animation: sable 1.5s ease-in-out infinite alternate;

}


@keyframes sable {
  from {
    box-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 15px #fff, 0 0 20px #228DFF, 0 0 35px #228DFF, 0 0 40px #228DFF, 0 0 50px #228DFF, 0 0 75px #228DFF;
  }
  to {
    box-shadow: 0 0 2.5px #fff, 0 0 5px #fff, 0 0 7.5px #fff, 0 0 10px #228DFF, 0 0 17.5px #228DFF, 0 0 20px #228DFF, 0 0 25px #228DFF, 0 0 37.5px #228DFF;
  }
}